* {
	margin: 0;
	padding: 0;
}

header {
	background-color: bisque;
}

.logo img {
	max-width: 80px;
}

.menu_logo {
	display: flex;
	max-width: 1200px;
	margin: auto;
	justify-content: space-between;
	align-items: center;
}

.menu_list ul {
	display: flex;
	list-style: none;
}

.menu_list ul li {
	margin-left: 20px;
}

.hero {
	background-image: url(./components/trans.webp);
	height: 500px;
	position: relative;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

.hero h1 {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	text-transform: uppercase;
	font-family: cursive;
}

.create_post {
	max-width: 800px;
	margin: auto;
	box-shadow: 3px 8px 15px #ccc;
	padding: 20px;
}

label {
	margin-bottom: 10px;
}

.form_wrap {
	margin: 50px 0;
}

.form_wrap h2 {
	text-align: center;
	margin-bottom: 20px;
}

input[type="submit"] {
	max-width: 300px;
}

.form_field input {
	width: 100%;
	height: 30px;
}

textarea {
	height: 90px;
	width: 100%;
}

footer {
	text-align: center;
	background-color: bisque;
	padding: 20px 0;
}

.social_links ul {
	display: flex;
	align-items: center;
	justify-content: center;
	list-style: none;
}

.social_links ul li img {
	max-width: 40px;
}

.Img {
	max-width: 100%;
	max-height: 270px;
	width: 100%;
	object-fit: cover;
}

.form_field {
	margin-bottom: 20px;
}

.row {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	max-width: 1200px;
	margin: auto;
	place-content: space-between;
}

.column {
	width: 32%;
	margin-bottom: 20px;
	border: 1px solid #ccc;
	box-shadow: 2px 3px 5px #ccc;
}

.info {
	padding: 20px;
}
.info h1 {
	font-size: 18px;
	margin-bottom: 10px;
}

.cardText {
	margin-bottom: 10px;
}
.wrapper {
	padding: 50px 0;
}
